import VueRouter from 'vue-router'
import Vue from 'vue'
import Home from '../components/Home'
import Movie from '../components/Movie'
 
Vue.use(VueRouter)
 
const routes = [
  {
    path: '',
    redirect: '/home'
  },
  {
    path: '/home',
    component: Home
  },
  {
    path: '/movie/:id',
    component: Movie
  }
]
const router = new VueRouter({
  routes,
  mode: 'history',
  linkActiveClass: 'active'
})
 
// 3.将router对象传入到Vue实例
export default router